.section-footer1 {
  background-color: $red;
  padding-top: 3rem;
  padding-bottom: 3rem;
  h5 {
    color: $white;
    font-weight: bold;
  }
  p {
    color: $white;
    font-weight: 200;
  }
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
    margin-bottom: .3rem;
    .items-menu {
      color: $white;
      font-weight: 200;
      text-decoration: none;
      &:hover {
        color: $yellow;
      }
    }
  }
  small {
    color: $text-gray;
  }
  .dropdown-menu {
    min-width: 145px !important;
    padding: 0 !important;
    border-radius: 1.25rem !important;
    border: none;
    margin-bottom: -3px !important;
    li {
      margin-bottom: 0 !important;
    }
  }
  .dropdown-item {
    color: $text-gray;
    background-color: $white !important;
    &:hover {
      color: $text-gray;
      background-color: $red !important;
    }
  }
  .btn-idiomas {
    color: $text-gray;
    border-radius: 1.2em;
    background-color: $white;
    text-align: left;
    max-width: 145px;
    .arrow-down {
      width: 0.8rem;
    }
    &.show {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }
  .logo-footer {
    width: 60%;
    @media (max-width: 767.98px) {
      width: 50%;
      max-width: 400px;
    }
  }
}
.section-mobil-footer {
  background-color: $red;
  color: $white;
  .accordion-button {
    color: $white;
    background-color: $red;
    font-weight: bold;
    &:focus {
      border-color: $white;
      box-shadow: none;
    }
    &:after {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23ffffff'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
    }
  }
  .accordion-body {
    background-color: $red;
    ul {
      list-style: none;
      color: $white;
      padding-left: 0;
      a {
        text-decoration: none;
        color: $white;
        &.hover {
          color: $yellow;
        }
      }
    }
  }
  .inf-oneup {
    padding: 1.5rem;
    .logo-footer {
      width: 30%;
      max-width: 160px;
    }
  }
}
.section-footer2 {
  background-color: $red;
  padding: .5rem;
  border-top: 1px solid rgba($color: $gray-light, $alpha: 0.5);
  p {
    color: $white;
    font-weight: 200;
  }
}