.btn-rounded {
  border-radius: 50px;
  line-height: 1.75;
}
.btn-custom {
  font-size: .8rem;
}
.btn-redgr-white {
  padding-left: 2rem;
  padding-right: 2rem;
  color: $white;
  font-weight: bold;
  background: linear-gradient(180deg, rgb(241, 76, 71) 0%, rgb(252, 48, 21) 100%);
  border: none;
  transition: all 0.3s;
  &:hover {
    color: $red;
    box-shadow: 0 0 1px 1px $red;
  }
  &:before {
    background: $white;
    border-radius: 50px;
  }
  &:focus {
    box-shadow: none;
    color: #e4002b;
    border: 1px solid $red;
  }
}
