.section-newsletter {
  background-color: $gray;
  padding-top: 4rem;
  padding-bottom: 4rem;
  .container {
    position: relative;
    p {
      color: $white;
      font-size: 1.5rem;
    }
    .input-grupo {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      width: 100%;
      & > .form-control {
        position: relative;
        flex: 1 1 auto;
        width: 1%;
        min-width: 0;
      }
      .btn {
        position: relative;
        z-index: 2;
      }
    }
    .form-control {
      &:focus {
        color: $gray;
        background-color: $white;
        border-color: $gray;
        outline: 0;
        box-shadow: 0 0 0 0.25rem rgba($color: $gray, $alpha: 0.25);
      }
    }
    .form-check-input{
      border-radius: 50px;
      margin-bottom: 1rem;
      &:checked {
        background-color: $red;
        border-color: $red;
      }
      &:focus {
        border-color: $red;
        outline: 0;
        box-shadow: none;
      }
    }
    .form-check-label{
      a {
        color: $white;
        text-decoration: none;
        transition: all 0.3s;
        &:hover {
          color: $red;
        }
      }
    }
    .placaNewsletter {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: $gray;
      text-align: center;
    }
  }
}