#navbar {
  display: none;
  @media (min-width: 1200px) {
    display: block;
  }
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1030;
  transition: all 0.3s;
  .navbar {
    background-color: transparent;
    transition: all 0.3s;
    @media (max-width: 1199.98px) {
      max-width: 100%;
    }
    a {
      color: white;
      font-weight: 400;
      font-size: .8rem;
    }
    .navbar-brand {
      font-size: 18px;
      padding-top: 5px;
      padding-bottom: 5px;
      @media (min-width: 1200px) {
        width: 25vw;
      }
      .logo-navbar {
        height: 60px;
      }
    }
    .navbar-toggler {
      font-size: 18px;
      border: none;
      &:focus {
        box-shadow: none;
      }
      .navbar-toggler-icon {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 1%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
      }
    }
    .navbar-nav {
      @media (min-width: 992px) {
        margin-right: 9rem;
      }
      @media (min-width: 1200px) {
        flex-direction: row;
        align-items: center;
      }
      .nav-item {
        padding-left: 1.3rem;
        padding-right: 1.3rem;
        @media (min-width: 1400px) {
          padding-left: 2rem;
          padding-right: 2rem;
        }
        .nav-link {
          color: $white;
          font-weight: 600;
          border-bottom: 2px solid transparent;
          text-align: center;
          &.active:before {
            left: 0;
            right: 0;
            background: $white;
          }
        }
        &:hover {
          .nav-link:before {
            background: $white;
          }
        }
        .btn-custom {
          width: 170px;
        }
      }
      .dropdown {
        .bg-dropdown-menu {
          color: #212529;
          background-color: #ffffffaa;
          border-radius: 10px;
        }
        .dropdown-menu {
          color: #212529;
          background-color: transparent;
          margin: 0;
          border: none;
          &:before {
            position: absolute;
            z-index: -1;
            content: "";
            left: 50px;
            top: -2px;
            border-style: solid;
            border-width: 0 10px 10px 10px;
            border-color: transparent transparent #ffffffaa transparent;
            transition-duration: .3s;
            transition-property: transform;
          }
          li:hover {
            color: #CA3F98;
          }
          .dropdown-item {
            padding: .5rem 0;
            &:hover {
              color: #CA3F98;
              background-color: transparent;
            }
            &:focus {
              color: #1e2125;
              background-color: transparent;
            }
            &:active {
              color: #1e2125;
              background-color: transparent;
            }
          }
          .item-title {
            list-style: none;
            padding-top: 0.5rem;
            font-size: 0.9rem;
            li {
              font-weight: bold;
              color: #6D60E9;
              a {
                color: #6D60E9;
                text-transform: uppercase;
                font-weight: bold;
              }
            }
          }
          .subitems {
            list-style-type: disc;
            li {
              a {
                color: #5b5b5c;
                &:hover {
                  color: #CA3F98;
                }
              }
            }
          }
        }
        .dropdown-multicol2 {
          width: 30em;
          .dropdown-col {
            display:inline-block;
            width: 32%;
          }
        }
      }
    }
    .icons-perfil {
      position: relative;
      font-size: 18px;
      text-align: center;
      a {
        margin: 0 .5rem;
        transition: all .3s;
        text-decoration: none;
        &:hover {
          color: $red;
        }
        .img-icons {
          width: 25px;
        }
        .notificacion-icon {
          position: absolute;
          font-size: .75em;
          font-weight: 700;
          line-height: 1;
          color: white;
          border-radius: 50%;
          background-color: #f7d054;
          padding: .25rem .4rem;
          top: 0;
          transform: translateY(-50%) translateX(-70%);
        }
      }
      @media (min-width: 1200px) {
        width: 20vw;
        text-align: end;
      }
      @media (min-width: 1600px) {
        width: 10vw;
        text-align: end;
      }
      .popup-login {
        position: absolute;
        width: 150px;
        right: 0;
        transform: translateX(55px);
        padding-top: 10px;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: calc(50% - 10px);
          top: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent #ffffff transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
        .d-grid {
          background-color: #ffffff;
          border-radius: 10px;
        }
      }
      #btn-shopping-cart {
        cursor: pointer;
      }
    }
  }
}

#navbar-mobile {
  background-color: #e4002b;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 50px;
  z-index: 1030;
  @media (min-width: 1200px) {
    display: none;
  }
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .img-logo {
      height: 45px;
    }
    .btn {
      img {
        width: 30px;
        height: 20px;
      }
    }
    .icons-perfil {
      position: relative;
      font-size: 18px;
      text-align: center;
      a {
        margin: 0 .2rem;
        transition: all .3s;
        text-decoration: none;
        &:hover {
          color: $red;
        }
        .img-icons {
          width: 25px;
        }
        .notificacion-icon {
          position: absolute;
          font-size: .75em;
          font-weight: 700;
          line-height: 1;
          color: white;
          border-radius: 50%;
          background-color: #f7d054;
          padding: .25rem .4rem;
          top: 0;
          transform: translateY(-30%) translateX(-70%);
        }
      }
      @media (min-width: 1200px) {
        width: 20vw;
        text-align: end;
      }
      @media (min-width: 1600px) {
        width: 10vw;
        text-align: end;
      }
      .popup-login {
        position: absolute;
        width: 103px;
        top: 30px;
        right: 0;
        transform: translateX(4px);
        padding-top: 10px;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: calc(50% - 10px);
          top: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent #ffffff transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
        .d-grid {
          background-color: #ffffff;
          border-radius: 10px;
          .btn-custom {
            width: 80px;
          }
        }
      }
      #btn-shopping-cart {
        cursor: pointer;
      }
    }
  }
  .menu-open {
    position: fixed;
    top: 0;
    left: -100%;
    width: 70%;
    background-color: #e4002beb;
    height: 100%;
    overflow-y: auto;
    padding: 1rem 1rem 3rem 1rem;
    z-index: 1;
    transition: all .7s;
    .icon-close {
      text-align: end;
      img  {
        width: 35px;
      }
    }
    .contenedor-menu {
      margin-top: 5rem;
      .title-menu {
        font-size: 3rem;
        color: $white;
        font-weight: bold;
        text-align: center;
      }
      ul {
        padding-left: 0;
        list-style: none;
        li {
          border-bottom: 1px solid #e2cdcd;
          padding: 1rem;
          &:nth-child(1) {
            border-top: none;
          }
          a {
            text-decoration: none;
            color: $white;
            font-weight: 500;
            font-size: 1.3rem;
            &:hover {
              color: $yellow;
            }
            &:focus {
              color: $yellow;
            }
          }
          .notificacion {
            display: inline-block;
            font-size: 1rem;
            font-weight: 700;
            line-height: 1;
            color: #fff;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: 50%;
            background-color: #f7d054;
            padding: .6rem .8rem;
            margin-left: .8rem;
          }
        }
      }
    }
  }
}
    